/* Fonts & Base Styles */
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #1e1e2f;
  color: #fff;
}

/* Container */
.app-container {
  text-align: center;
}

/* Navbar */
.navbar {
  background-color: #3b4cca;
  padding: 2rem 1rem;
  border-bottom: 4px solid #ffcc00;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.navbar h1 {
  margin: 0;
  font-family: 'Bungee', cursive;
  font-size: 2.8rem;
  color: #ffcc00;
  text-shadow: 2px 2px #1b1b1b;
  letter-spacing: 2px;
  animation: popIn 0.6s ease-in-out;
}

/* Animation */
@keyframes popIn {
  0% {
    opacity: 0;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

/* Content Wrapper */
.content {
  padding: 2rem;
}

/* Search Bar */
.search-box {
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border-radius: 10px;
  border: none;
  outline: none;
  width: 90%;
  max-width: 400px;
  margin-bottom: 2rem;
  background-color: #2a2a3e;
  color: #fff;
  box-shadow: 0 0 10px rgba(255, 156, 84, 0.3);
}

/* Grid layout similar to the original */
.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
  justify-items: center;
}

/* Individual card styling */
.card {
  background-color: #2c2c3e;
  border-radius: 12px;
  padding: 1rem;
  max-width: 280px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4);
  transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 24px rgba(255, 156, 84, 0.3);
}

.card img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 0.75rem;
}

.card h2 {
  font-size: 1.25rem;
  color: #ff9c54;
  margin: 0.5rem 0 0.3rem;
}

.card p {
  font-size: 0.95rem;
  color: #ccc;
  margin: 0.25rem 0;
}

.loading-text {
  font-size: 1.5rem;
  color: #aaa;
}
.pagination {
  margin-top: 2rem;
}

.page-btn {
  background-color: #3b4cca;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  margin: 0 0.25rem;
  border-radius: 6px;
  cursor: pointer;
  transition: 0.2s;
}

.page-btn:hover {
  background-color: #ff9c54;
  color: #1e1e2f;
}

.page-btn.active {
  background-color: #ff9c54;
  font-weight: bold;
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(30, 30, 30, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #2c2c3e;
  padding: 2rem;
  border-radius: 10px;
  text-align: center;
  max-width: 400px;
  width: 90%;
  box-shadow: 0 0 15px rgba(255, 156, 84, 0.4);
}

.modal-content img {
  width: 100%;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.modal-close {
  margin-top: 1rem;
  background-color: #3b4cca;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
}
.favorite-star {
  font-size: 1.5rem;
  color: #ccc;
  cursor: pointer;
  margin-bottom: 0.5rem;
  display: inline-block;
  transition: color 0.2s;
}

.favorite-star.favorited {
  color: #ffcc00;
}
